.form-container {
    max-width: 800px;
    margin: 20px auto;
    padding: 20px;
    background-color: white;
    border-radius: 8px;
    box-shadow:  0 2px 5px rgba(0, 0, 0, 0.1);
    display: flex;
    flex-direction: column;
    align-items: center;
}
.form-container input {
    padding: 8px;
    border: 1px solid #cccccc;
    border-radius: 4px;
    margin-bottom: 10px;
}
.form-container button {
    padding: 10px;
    border: 1px solid #cccccc;
    border-radius: 4px;
    background-color: #1e88e5;
    color: white;
    border: none;
    cursor: pointer;
    margin-bottom: 10px;
    width: 200px;
    margin: 0 auto;
}
.form-container button:hover {
    background-color: #1b77c7;
    color: white;
}
